.on-map__wrapper {
  text-align: left;

  background-color: transparent;

  &--theme-light {
    background-color: $white;
  }
}

.on-map__cooperation {
  margin: 0;
  width: 130px;
  margin-right: 10px;

  @include text(16px, 20px);
  color: $black;
  text-transform: uppercase;

}

.on-map__address {
  margin: 0;
  width: 52%;

  font-family: $secondary-font;
  @include text(14px, 20px);
}

.on-map__iframe {
  width: 100%;
  min-height: 363px;

  vertical-align: middle;

  border: none;
}

.on-map__container {
  padding-top: 26px;
  padding-bottom: 26px;
  display: flex;

  text-align: left;
}

.on-map__wrap {
  display: block;
}

@media (min-width: $tablet-width) {
  .on-map__container {
    padding: 60px 70px;
    width: 628px;
    justify-content: space-between;
    align-items: center;
  }

  .on-map__cooperation {
    margin: 0;
    width: 155px;

    @include text(20px, 25px);
  }

  .on-map__address {
    width: 155px;
    align-items: flex-end;

    @include text(16px, 20px);
    text-align: right;
  }

  .on-map__wrap {
    margin-top: 20px;
  }

  .on-map__iframe {
    height: 400px;
  }
}

@media (min-width: $desktop-width) {
  .on-map__container {
    position: absolute;
    z-index: 1;
    top: 95px;

    padding-left: 76px;
    padding-right: 89px;
    padding-top: 62px;
    padding-bottom: 62px;
    width: 570px;
    box-sizing: border-box;

    background: $white;
  }

  .on-map__address {
    align-items: flex-start;

    text-align: left;
  }

  .on-map__iframe {
    position :absolute;
    left: calc(50% - 50vw);
    top: 0;

    width: 100vw;
  }
}
